/* Reset some default styles */
@import url('https://fonts.googleapis.com/css?family=Roboto:300');
body, h1, h2, h3, p, ul, li {
  margin: 0;
  padding: 0;
}

/* Set up basic styles */


.App {
  text-align: center;
  padding: 20px;
  background-color:  #000000; /* Requirement 1 */
  background-image: url('D:\Projects\Portfofilo React\portfolio\public\images\banner.jpg');
}

.App-header {
   /* Requirement 2 */
  
  padding: 40px; /* Requirement 4 */
  margin: 25px 0;
  
  
  border-bottom: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
}




button {
  text-decoration: none;
  width: auto; /* Adjust the width as needed */
  padding: 10px 20px; /* Adjust the padding as needed */
  color: #000000;
  font-size: 1em; /* Adjust the font size as needed */
  font-weight: 700;
  letter-spacing: 0.2em; /* Adjust the letter spacing as needed */
  z-index: 20;
  transition: all 0.3s ease-in-out;
  border-radius: 40px;
}
 button:hover  {
  color: #59f4c0;
  animation: scaleUp 0.3s ease-in-out;
 }
 @keyframes scaleUp {
  0% {
   transform: scale(1);
  }
 
  50% {
   transform: scale(0.95);
  }
 
  100% {
   transform: scale(1);
  }
 }
 

section {
  /* Requirement 2 */
  padding: 200px; /* Requirement 4 */
  
  border-radius: 100px;
  
  color: #ffffff;

}

footer {
  margin-top: 20px;
  
  color: #ffffff;
  padding: 5px;
  
  border-radius: 40px;
}

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;800&display=swap");

:root {
  --bg: #cfc4c4;
  --clr-1: #00c2ff;
  --clr-2: #33ff8c;
  --clr-3: #ffc640;
  --clr-4: #e54cff;

  --blur: 1rem;
  --fs: clamp(3rem, 8vw, 7rem);
  --ls: clamp(-1.75px, -0.25vw, -3.5px);
}



*,
*::before,
*::after {
  font-family: inherit;
  box-sizing: border-box;
}



.name-text {
  font-size: clamp(2rem, 6vw, 4rem);
  font-weight: 800;
  letter-spacing: var(--2s);
  position: relative;
  overflow: hidden;
  color: #ff0000;
  margin: 0;
}






/* Media query for smaller screens */
@media (max-width: 600px) {
  .Profile-picture {
    width: 100px;
    height: 100px;
  }
}




table {
  width: 100%;
  height: 70%;
  border-collapse: collapse;
  margin-top: 20px;
  font-family: Arial, sans-serif;
}

th, td {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

th {
  background-color: #f5e6e6; /* Table Header Background Color */
  color: #010101; /* Table Header Text Color */
}

tr:nth-child(even,odd) {
  background-color: #151414; /* Alternate Row Background Color */
}

tr:hover {
  background-color: #1b1c1a; /* Hovered Row Background Color */
}
/* For screens up to 600px wide */
@media (max-width: 600px) {
  .App {
    padding: 10px;
  }
  
  .App-header {
    padding: 20px;
    margin: 10px 0;
  }
  
  /* Adjust other styles as needed */
  .card {
    width: 190px;
    height: 254px;
    margin: 0 auto;
    background-color: #011522;
    border-radius: 8px;
    z-index: 1;
  }
  
  .tools {
    display: flex;
    align-items: center;
    padding: 9px;
  }
  
  .circle {
    padding: 0 4px;
  }
  
  .box {
    display: inline-block;
    align-items: center;
    width: 10px;
    height: 10px;
    padding: 1px;
    border-radius: 50%;
  }
  
  .red {
    background-color: #ff605c;
  }
  
  .yellow {
    background-color: #ffbd44;
  }
  
  .green {
    background-color: #00ca4e;
  }
  
  .card__content {
    padding: 10px;
    color: #fff;
  }
  
  .Certifications {
   /* Use the correct path */
    background-size: cover;
    background-position: center;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 0px 5px rgb(49, 49, 49);
  }
}

@import url('https://fonts.googleapis.com/css?family=Roboto:700');

@import url("https://fonts.googleapis.com/css2?family=Alumni+Sans:wght@400;600;700&display=swap");

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}



section1 {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	min-height: 10vh;
	font-size: 2em;
	
	color: #fff;
	text-transform: uppercase;
	gap: 30px;
}

h1 {
	font-size: 2.5em;
	
	@media (max-width: 768px) {
		font-size: 1.3em;
	}
}

.text-shadow {
  font-style: italic;
  text-transform: uppercase;
  color: transparent;
  -webkit-text-stroke: #fff;
  -webkit-text-stroke-width: 1px;
  text-shadow: 2px 2px 10px blue;
  transition: all 0.5s ease-in-out;
  text-align: center;
  letter-spacing: 0.2em;
  animation: flicker 0.5s ease-in-out infinite alternate;
}

.text-shadow:hover {
  color: #fff;
}

.tags small {
  font-size: 0.5em;
}

@keyframes flicker {
  0% {
    opacity: 0.5;
    text-shadow: 2px 2px 10px blue;
  }
  100% {
    opacity: 1;
    text-shadow: 2px 2px 20px blue;
  }
}
